.profile {
    padding-top: 20vh;
}

.profile button {
    width: 40%;
    height: 3rem;
    border-radius: 10px;
    font-size: 5vh;
    background-color: darkviolet;
    color: white;
}

@media (max-width: 768px) {
    .profile button {
        width: 60%;
        font-size: 5vh;
    }
}